import { Component, h, Element, State, Host } from '@stencil/core';
import { ICellRendererComp, ICellRendererParams } from 'ag-grid-community';
import {
    ColorCell,
    IClickCell,
    ITooltipCell,
} from '../../../../types/table-cells';

export interface IChipCellParams extends ITooltipCell, IClickCell, ColorCell {}

interface InitParams extends IChipCellParams, ICellRendererParams {}

/**
 * @category Table Cell
 * @description A cell component for displaying a list of chips.
 */

@Component({
    tag: 'atui-chip-list-cell',
    shadow: false,
})
export class AtuiChipListCell implements ICellRendererComp {
    @Element() el: HTMLElement;
    @State() params: InitParams;

    init(params: InitParams): void {
        this.params = params;
    }

    getGui(): HTMLElement {
        return this.el;
    }

    refresh(params: InitParams): boolean {
        this.updateCell(params.value);
        return true;
    }

    protected updateCell(newValue: any) {
        this.params.value = newValue;
    }

    render() {
        return (
            <Host class="flex h-full items-center leading-[100%]">
                <atui-chip-list
                    chips={this.params.value}
                    show_clear_all={false}
                    disabled={true}
                ></atui-chip-list>
            </Host>
        );
    }
}
